* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

a {
    transition: all 0.3s ease-in-out;
}

body {
    letter-spacing: 0.15px;
    background: $body-bg ;
    color: $white;
    font-family: $font-family-sans-serif;
    display: flex;
    flex-direction: column;
    height: 100vh;
    line-height: 1.5;
}

.loader {
    .loading & {
        display: flex;
    }

    display: none;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;
    position: absolute;
    width: 100%;
    top: 0;
    z-index: 99999;
    background: $body-bg ;

    .logo-char {
        margin-bottom: 20vh;

    }
}

#header {
    background: #34393F;
    padding: 18px 15px;
    position: sticky;
    top: 0;
    z-index: 1020;
    display: flex;
    justify-content: space-between;
    .logo{
        @include respond-below(md){
           order: 0;
        }
    }
    .course-setting{
        @include respond-below(md){
            order: 1;
         }
    }
}


.breadcrumb {
    margin-bottom: 0;
    list-style: none;
    display: flex;
    align-items: center;
    line-height: 1;

    li {
        @include respond-below(md){
            display: none;
        }
        &:last-child{
            @include respond-below(md){
                display: block;
            }
        }
        &:not(:last-child) {
            padding-right: 10px;

            &:after {
                content: "›";
                padding-left: 10px;
            }
        }

        a {
            color: $white;

            &:hover {
                color: rgba($color: #fff, $alpha: 0.5);
            }
        }
    }
}


.account-setting {
    @include respond-below(md){
        display: none;
    }
    .dropdown {
        position: relative;
    }

    .dropdown-toggle {
        display: flex;
        align-items: center;
    }

    .dropdown-menu {
        position: absolute;
        border: 1px solid rgba($color: #fff, $alpha: 0.3);
        width: 200px;
        right: 20px;
        border-radius: 6px;
        list-style: none;
        background: $body-bg ;
        padding: 10px 0;
        top: 55px;
        margin: 0;
        display: none;

        &.show {
            display: block;
        }

        .dropdown-item {
            padding: 7px 20px;
            display: block;
            color: white;
            text-decoration: none;
            font-size: 14px;

            &:hover {
                background: #34393F;
                color: $white;
            }
        }
    }

    .dropdown-toggle {
        text-decoration: none;
        color: $white;

        &::after {
            display: none;
        }

        .username {
            padding: 0 5px;
        }

        svg {
            fill: $white;
        }
    }




}

.main {
    flex: 1;
    display: flex;
    height: 100%;
    overflow: hidden;
}

.editor {
    margin: 20px 0 20px 0;
    padding-left: 10px;
    flex: 1;
    overflow: scroll;
    position: relative;
    -ms-overflow-style: none;
    /* IE and Edge */
    scrollbar-width: none;
    &.editor-hide{
        @include respond-below(md){
            display: none;
        }
    }

    /* Firefox */
    textarea {
        line-height: inherit;
        white-space: pre-wrap;
        word-break: keep-all;
        background: none;
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 1;
        resize: none;
        caret-color: black;
        padding: inherit;
        outline: none;
        font-size: inherit;
        font-family: inherit;
        box-sizing: border-box;
        border: medium none;
        top: 0px;
        left: 0px;
        color: inherit;
        overflow: hidden;
        -webkit-text-fill-color: transparent;
    }

    pre {
        font-size: 16px;
        outline: none;
    }

    .line-no {
        position: absolute;
        opacity: .3;
        left: 10px;

        &.error {
            opacity: 1;

            &::before {
                background: url(../img/indicator.svg) no-repeat;
                opacity: 1;
                content: "";
                height: 24px;
                width: 33px;
                display: inline-block;
                position: absolute;
                right: -13px;
                z-index: -1;
                top: 2px;
            }

            &:after {
                background: #0057a8;
                opacity: 1;
                content: "";
                height: 24px;
                width: 20px;
                display: inline-block;
                position: absolute;
                left: -10px;
                z-index: -1;
                top: 2px;
            }
        }
    }

    .function {
        color: #F2E4A4;
    }

    .function {
        color: #F2E4A4;
    }
    .btn{
        position: fixed;
        bottom: 20px;
        display: none;
        @include respond-below(md){
            bottom: 10px;
            display: block;
            width: calc(100% - 10px);
            left: 5px;
        }
    }
}

img {
    width: auto;
    max-width: 100%;
}

.output {
    width: 30%;
    display: flex;
    flex-direction: column;
    background: #fff;
    border-radius: 6px;
    margin: 20px;
    border-top: 23px solid #D9D9D9;
    @include respond-below(md){
        display: none;
        border-top: 0;
    }
    &.output-show{
        @include respond-below(md){
            display: flex;
            width: 100%;
            margin: 0;
        }
        .btn{
            display: none;
            @include respond-above(md){
                display: block;
                }
        }
        .btn-code{
            @include respond-below(md){
            display: block;
            }
            display: none;
        }
    }

    .canvas {
        min-height: 50%;
        border: 1px solid #E1E1E1;
        overflow: hidden;
    }

    .console {
        flex: 1;
        border: 1px solid #CBCBCB;
        color: #000;
    }

    .execute {
        padding: 10px;
    }


}

.btn-primary {
    background: #EAEAEA;
    box-shadow: 0px 4px 0px #6A6A6A, 0px 16px 12px -8px rgba(0, 0, 0, 0.4), inset 0px -2px 1px 1px #6A6A6A;
    border-radius: 10px;
    font-weight: 700;
    font-size: 20px;
    line-height: 56px;
    text-align: center;
    text-transform: capitalize;
    font-family: $font-family-sans-serif;
    color: #545454;
    border: none;
    width: 100%;
    display: block;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    height: 52px;
    text-decoration: none;
    @include respond-below(md){
        line-height: 45px;
    }

    &:hover {
        background: #cecece;
    }

    &.btn-loading {
        line-height: 40px;
        /* rainbow */
        background: linear-gradient(270deg, #FF5757 0%, #9F9368 29.68%, #60A04A 51.75%, #64A591 77.59%, #6E83BA 100%);
        box-shadow: 0px 4px 0px #6A6A6A, 0px 16px 10px -8px rgba(0, 0, 0, 0.4), inset 0px -2px 1px 1px #6A6A6A;

        .txt {
            display: none;

        }

        .onplay {
            margin-top: -10px;
            display: block;

            span {
                height: 5px;
                width: 5px;
                border-radius: 50%;
                display: inline-block;
                background-color: #fff;
                margin: 0 5px;
            }
        }
    }
}

.console {
    font-family: $font-code;
    padding: 20px;

    .heading {
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 140%;
        color: rgba(0, 0, 0, 0.8);
    }

    .error {
        font-weight: 500;
        font-size: 16px;
        text-decoration-line: underline;
        color: rgba(0, 87, 168, 0.8);
    }
}

.popup-modal {
    position: absolute;
    max-width: 460px;
    z-index: 99999;
    left: calc(50% - 30px);
    top: 50%;
    transform: translate(-50%, -50%);
    width: calc(100% - 60px);
    background: #232527;
    box-shadow: 0 2px 0 1px #000;
    border-radius: 6px;
    font-size: 14px;
    padding: 40px;
    margin: 30px;


    a {
        color: #fff;
    }

    h3 {
        margin-bottom: 30px;
    }

    .logo-holder {
        margin-top: 50px;
    }

    .btn-primary {
        font-size: 16px;
        height: auto;
    }

    .close-modal {
        position: absolute;
        right: 20px;
        top: 20px;

        svg {
            height: 18px;
            width: 18px;
        }

    }
}


.overlay {
    background: rgba(26, 27, 29, 0.9);
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 9999;
}

.form-field {
    margin-bottom: 25px;
    position: relative;

    input {
        background: #FFFFFF;
        border-radius: 6px;
        color: rgba(0, 0, 0, 0.6);
        border: none;
        padding: 13px 15px;
        width: 100%;
        font-size: 14px;
    }

    .revel-pass {
        height: 15px;
        width: 22px;
        right: 10px;
        top: 15px;
        border-radius: 0px;
        position: absolute;
        cursor: pointer;

    }
}

.form-text {
    margin-bottom: 20px;
}

.text-center {
    text-align: center;
}

.text-right {
    text-align: right;
}

.mb3 {
    margin-bottom: 30px;
}

.course-modal {
    background: rgba(26, 27, 29, 0.9);
    position: absolute;
    top: 0;
    width: 100%;
    height: 100vh;
    z-index: 9999;
    overflow: scroll;
    -ms-overflow-style: none;
    /* IE and Edge */
    scrollbar-width: none;

    .course-head {
        border-bottom: 1px solid rgba(255, 255, 255, 0.3);
        padding: 15px;
        text-align: right;
    }

    .close-modal {
        svg {
            margin-top: 5px;
            height: 18px;
            width: 18px;
        }

    }

    .course-body {
        padding: 30px;
        display: flex;
        gap: 20px;
        flex-wrap: wrap;
    }

    .course-col {
        flex: 1;
        @include respond-between(sm, md) {
            flex: 0 0 48%;
        }
        @include respond-below(sm) {
            flex: 0 0 100%;
        }
        .course-cat {
            font-family: 'Inter';
            font-style: normal;
            font-weight: 700;
            font-size: 16px;
            margin-bottom: 15px;
        }

        ul {
            padding-left: 5px;
            margin-bottom: 0;
            list-style: none;

            ul {
                margin-top: 10px;
                padding-left: 20px;
                position: relative;

                &::before {
                    content: "";
                    position: absolute;
                    height: 2px;
                    left: 8px;
                    top: 8px;
                    width: 20px;
                    background: #595959;

                }
            }

            li {
                padding-bottom: 10px;
                position: relative;

                &:last-child {
                    padding-bottom: 0;

                    a {
                        &::after {
                            display: none;
                        }
                    }
                }

                &.done {
                    >a {
                        &::before {
                            background: #3CB679;
                            border-color: #3CB679;
                            content: "✔";
                            font-size: 12px;
                            line-height: 1;
                            text-align: center;
                        }
                    }
                }


                a {
                    padding-left: 22px;
                    position: relative;
                    color: #fff;
                    text-decoration: none;

                    display: inline-block;

                    &::before {
                        content: "";
                        position: absolute;
                        height: 14px;
                        left: 0;
                        top: 2px;
                        width: 14px;
                        border: 2px solid #595959;
                        border-radius: 50%;
                        display: inline-block;
                        background: #212121;
                        z-index: 1;
                    }

                    &::after {
                        content: "";
                        position: absolute;
                        height: 100%;
                        left: 8px;
                        top: 18px;
                        width: 2px;
                        background: #595959;
                    }
                }
            }
        }

    }
}
.hide{
    display: none;
}
.show{

display: block;
}