$font-size-root :16px;
$font-family-sans-serif : 'Inter', sans-serif;
$font-code: 'Fira Code', monospace;

$body-bg : #232527;

$container-max-widths: (
  sm: 540px,
  md: 720px,
  lg: 960px,
  xl: 1140px,
  xxl:1220px
);


$primary-color: #172325;
$light-primary: #319AA8;
$primary: #0F7080;


$white: #ffffff;
$black: #000000;
$black-20: rgba(0, 0, 0, 0.2);
$black-60: rgba(0, 0, 0, 0.6);
$black-80: rgba(0, 0, 0, 0.8);

$white-20: rgba(255, 255, 255, 0.2);
$white-60: rgba(255, 255, 255, 0.6);
$white-80: rgba(255, 255, 255, 0.8);

$theme-toggle-color: rgba(216, 221, 222, 0.8);

$progress-active: $light-primary;
$progress-indicator-bg: $primary;
$dropdown-min-width: 10.714rem;

$text-color: $black-80;
$text-color-dark: $white-80;

$border-color: $black-20;
$border-color-dark: $white-20;

$filter-border-color: $black-20;
$filter-label-color: $black;
$filter-color: $black-80;

$filter-border-color-dark: $white-80;
$filter-label-color-dark: $white-80;
$filter-color-dark: $white;